import { Link } from "react-router-dom";

export default function PostList({issues}) {

  return (
    <ul>
        {issues.map((issue) => (
          <li key={issue.id}>
            <Link to={"/postdetail"} state={{ data: issue }}>
              <p> : {issue.updated_at}</p>
              <h1> : {issue.title}</h1>
              <h3> : {issue.body}</h3>
              { issue.milestone && <p> : {issue.milestone.title}</p>}
            </Link>
          </li>
        ))}
      </ul>
  );
}

